"use client";

import Link from "next/link"
import {useRouter} from "next/navigation";

type Props = {
    showLoginButton: boolean;
};

/*export default: Hace que el componente sea visible en todo el proyecto */
export default function Header({showLoginButton = true}: Props){
    const router = useRouter();  
    return 

    <header className="site-header">
        <nav className="container nav">
          <a className="brand ml-10">Logo</a>
          <ul className = "menu">   

          {/*Opciones con enlaces a una nueva ventana*/}
          <Link
          href="/sign-up"
          className="ml-1 inline-flex items-center rounded-xl">     
          <li><a> Caracteristicas </a></li>   
          </Link> 

          <Link
          href="/sign-up"
          className="ml-1 inline-flex items-center rounded-xl"> 
          <li><a> Precios </a></li>
          </Link> 

          <Link
          href="/sign-up"
          className="ml-1 inline-flex items-center rounded-xl">
          <li><a> FAQ </a></li>
          </Link>

        {showLoginButton && (
            <Link
            href="/log-up"
            className="ml-1 inline-flex items-center rounded-xl">
            <li><a> Iniciar Sesion </a></li>
          </Link>
        )}
          

          </ul>
        </nav>
      </header>
}